import { ConfigProvider, Image, Layout } from 'antd';
import 'antd/dist/antd.css';
import React from 'react';
import ReactDOM from 'react-dom';
import { Route, HashRouter as Router, Routes } from 'react-router-dom';
import './index.css';
import SSAPage from './SSAPage';

const App = () => {
  const NODE_ENV = process.env.NODE_ENV;
  if (NODE_ENV !== 'development') {
    console.log = function () {};
  }

  return (
    <Router>
      <ConfigProvider>
        <Layout
          className="layout"
          style={{ backgroundColor: 'rgba(0,0,0,0)', minWidth: '320px' }}
        >
          <Image
            preview={false}
            style={{ zIndex: -1, position: 'absolute', top: 0 }}
            width={'100%'}
            src="/pics/Header BG.png"
          />
          <Routes>
            <Route path="/" element={<SSAPage />} />
          </Routes>
          <Image
            style={{
              zIndex: -2,
              position: 'absolute',
              height: '2000px',
              bottom: '0px',
            }}
            preview={false}
            src="/pics/Bubble BG.png"
          />
        </Layout>
      </ConfigProvider>
    </Router>
  );
};

ReactDOM.render(<App />, document.getElementById('root'));
