import { Table, Tag, Tooltip, Card } from 'antd';
import React, { useEffect, useState } from 'react';
import './table.css';

const SSATable = () => {
  const [data, setData] = useState([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    // Load SSA data from CSV file
    fetch(`${process.env.PUBLIC_URL}/SSA_data.csv`)
      .then(response => response.text())
      .then(text => {
        const rows = text.split('\n').filter(row => row.trim());
        const headers = rows[0].split(',');
        const tableData = rows.slice(1).map((row, index) => {
          const values = row.split(',');
          const rowData = { key: index };
          headers.forEach((header, i) => {
            rowData[header.trim()] = values[i]?.trim() || '';
          });
          return rowData;
        });
        setData(tableData);
        setLoading(false);
      })
      .catch(error => {
        console.error('Error loading SSA data:', error);
        setLoading(false);
      });
  }, []);

  const columns = [
    {
      title: 'Rank',
      dataIndex: 'rank',
      key: 'rank',
      width: 60,
      render: (rank) => {
        const rankNum = parseInt(rank);
        if (rankNum === 1) return <Tag color="gold"> {rank}</Tag>;
        if (rankNum === 2) return <Tag color="silver"> {rank}</Tag>;
        if (rankNum === 3) return <Tag color="#cd7f32"> {rank}</Tag>;
        return rank;
      }
    },
    {
      title: 'Model',
      dataIndex: 'model',
      key: 'model',
      width: 150,
      render: (model, record) => (
        <a href={record.url} target="_blank" rel="noopener noreferrer">
          {model}
        </a>
      )
    },
    {
      title: 'F-score',
      dataIndex: 'F-score',
      key: 'f-score',
      width: 80,
      render: (value) => <strong>{value}</strong>,
      sorter: (a, b) => parseFloat(a['F-score']) - parseFloat(b['F-score']),
    },
    {
      title: <Tooltip title="Safety score at first attempt">Safe@1</Tooltip>,
      dataIndex: 'Safe@1',
      key: 'safe1',
      width: 80,
      render: (value) => `${value}%`,
      sorter: (a, b) => parseFloat(a['Safe@1']) - parseFloat(b['Safe@1']),
    },
    {
      title: <Tooltip title="Safety score with multiple attempts">Safe@k</Tooltip>,
      dataIndex: 'Safe@k',
      key: 'safek',
      width: 80,
      render: (value) => `${value}%`,
      sorter: (a, b) => parseFloat(a['Safe@k']) - parseFloat(b['Safe@k']),
    },
    {
      title: <Tooltip title="Thinking capability at first attempt">think@1</Tooltip>,
      dataIndex: 'think@1',
      key: 'think1',
      width: 80,
      render: (value) => `${value}%`,
      sorter: (a, b) => parseFloat(a['think@1']) - parseFloat(b['think@1']),
    },
    {
      title: <Tooltip title="Thinking capability with multiple attempts">think@k</Tooltip>,
      dataIndex: 'think@k',
      key: 'thinkk',
      width: 80,
      render: (value) => `${value}%`,
      sorter: (a, b) => parseFloat(a['think@k']) - parseFloat(b['think@k']),
    },
    {
      title: <Tooltip title="Operating Systems score">OS@1</Tooltip>,
      dataIndex: 'OS@1',
      key: 'os1',
      width: 70,
      render: (value) => `${value}%`,
      sorter: (a, b) => parseFloat(a['OS@1']) - parseFloat(b['OS@1']),
    },
    {
      title: <Tooltip title="Operating Systems score (k attempts)">OS@k</Tooltip>,
      dataIndex: 'OS@k',
      key: 'osk',
      width: 70,
      render: (value) => `${value}%`,
      sorter: (a, b) => parseFloat(a['OS@k']) - parseFloat(b['OS@k']),
    },
    {
      title: <Tooltip title="Computer Science score">CS@1</Tooltip>,
      dataIndex: 'CS@1',
      key: 'cs1',
      width: 70,
      render: (value) => `${value}%`,
      sorter: (a, b) => parseFloat(a['CS@1']) - parseFloat(b['CS@1']),
    },
    {
      title: <Tooltip title="Computer Science score (k attempts)">CS@k</Tooltip>,
      dataIndex: 'CS@k',
      key: 'csk',
      width: 70,
      render: (value) => `${value}%`,
      sorter: (a, b) => parseFloat(a['CS@k']) - parseFloat(b['CS@k']),
    },
    {
      title: <Tooltip title="Robotics score">RO@1</Tooltip>,
      dataIndex: 'RO@1',
      key: 'ro1',
      width: 70,
      render: (value) => `${value}%`,
      sorter: (a, b) => parseFloat(a['RO@1']) - parseFloat(b['RO@1']),
    },
    {
      title: <Tooltip title="Robotics score (k attempts)">RO@k</Tooltip>,
      dataIndex: 'RO@k',
      key: 'rok',
      width: 70,
      render: (value) => `${value}%`,
      sorter: (a, b) => parseFloat(a['RO@k']) - parseFloat(b['RO@k']),
    },
  ];

  return (
    <div className="table">
      <div className="table-content">
        <Card bordered={false} style={{ width: '100%' }}>
          <Table
            columns={columns}
            dataSource={data}
            loading={loading}
            pagination={false}
            className="ssa-table"
          />
        </Card>
      </div>
    </div>
  );
};

export default SSATable;