import React from 'react';

const SSADictionary = (props) => {
  const redirectToArxiv = () => {
    window.open('https://arxiv.org/abs/2505.19690', '_blank');
  };

  return (
    <>
      {props.type === 'word' ? (
        <div style={{ width: '100%', cursor: 'pointer' }} onClick={redirectToArxiv}>
          Paper
        </div>
      ) : (
        <img
          style={{ height: '48px', width: '48px', cursor: 'pointer' }}
          onClick={redirectToArxiv}
          src={`${process.env.PUBLIC_URL}/pics/Dic.png`}
          alt="Paper"
        />
      )}
    </>
  );
};

export default SSADictionary;