import React from 'react';

const SSADashboard = (props) => {
  const redirectToBSA = () => {
    window.open('https://openstellarteam.github.io/BSA/', '_blank');
  };

  return (
    <>
      {props.type === 'word' ? (
        <div style={{ width: '100%', cursor: 'pointer' }} onClick={redirectToBSA}>
          Dashboard
        </div>
      ) : (
        <img
          style={{ height: '48px', cursor: 'pointer' }}
          onClick={redirectToBSA}
          src={`${process.env.PUBLIC_URL}/pics/Board.png`}
          alt="Dashboard"
        />
      )}
    </>
  );
};

export default SSADashboard;